/* Module: S */
.main_list {
    transition: all 0.5s ease-in-out;
    padding: 10px 10px 80px 10px;
    .articles_list {
        list-style: none;
        margin: 0;
        padding: 0;
    }
    .articles_list--element {
        clear: both;
        background-color: $white;
        -webkit-box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.2);
        -moz-box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.2);
        box-shadow: 0px 1px 10px 0px rgba(0, 0, 0, 0.2);
        height: 100px;
        overflow: hidden;
        margin-bottom: 10px;
        .img_wrapper {
            background: url({{file_dest}}/img_wrapper.png) center center/cover no-repeat;
            position: relative;
            padding-bottom: 100px;
            height: 0;
            overflow: hidden;
            margin-bottom: 0;
            width: 100px;
            float: left;
            .image {
                background-position: center center;
                background-size: cover;
                background-repeat: no-repeat;
                position: absolute;
                top: 0px;
                left: 0px;
                bottom: 0px;
                right: 0px;
            }
        }
        .content_wrapper {
            position: relative;
            float: left;
            width: calc(100% - 100px);
            height: 100px;
            padding: 8px 10px;
            .article-cathegory {
                margin: 0px;
                margin-top: -4px;
                font-size: pxToRem(10);
                color: $blue;
                font-weight: $semibold;
                max-height: 18px;
                overflow: hidden;
            }
            h3 {
                color: black;
                margin: 2px 0px;
                font-size: pxToRem(18);
                font-weight: $regular;
            }
            .reading_time {
                margin: 0px;
                font-size: pxToRem(12);
                color: $gray-darkest;
                position: absolute;
                bottom: 0;
                left: 10px;
                line-height: 20px;
            }
            .timestamp {
                margin: 0px;
                font-size: pxToRem(10);
                color: $gray-darkest;
                position: absolute;
                bottom: 0;
                right: 10px;
                line-height: 20px;
            }
        }
    }
    .articles_list--element-video, .articles_list--element-featured {
        @extend .articles_list--element;
        background-color: black;
        height: auto;
        .img_wrapper {
            padding-bottom: 146px;
            width: 100%;
            .image {
                background-position: center center;
                background-size: cover;
                background-repeat: no-repeat;
                position: absolute;
                top: 0px;
                left: 0px;
                bottom: 0px;
                right: 0px;
            }
        }
        .content_wrapper {
            padding: 8px 10px 3px 10px;
            color: $white;
            width: 100%;
            height: auto;
            h3 {
                color: $white;
                margin: 0 0 13px 0;
            }
            .article-cathegory {
                color: $white;
                font-weight: $regular;
                float: left;
            }
            .timestamp {
                color: $white;
                font-weight: $regular;
                position: initial;
                float: right;
                margin-top: -4px;
            }
        }
    }
    .articles_list--element-video {
        .img_wrapper {
            &:before {
                content: " ";
                position: absolute;
                width: 26px;
                height: 36px;
                z-index: 1;
                top: calc(50% - 18px);
                left: calc(50% - 13px);
                background: url({{file_dest}}/playbutton.svg) center center/contain no-repeat;
            }
        }
    }
}